/* eslint-disable */
import ng from 'vendor-bb-angular';

import bbStorageModuleKey, { bbStorageServiceKey } from 'lib-bb-storage-ng';
import auditData from './data-bb-audit-http';

const auditDataModuleKey = 'data-bb-audit-http-ng';

export const auditDataKey = 'data-bb-audit-http-ng:auditData';

export default ng
  .module(auditDataModuleKey, [
    bbStorageModuleKey,
  ])
  .provider(auditDataKey, [() => {
    const config = {
      baseUri: '/',
    };

    return {
      setBaseUri: (baseUri) => {
        config.baseUri = baseUri;
      },
      $get: [
        '$q',
        bbStorageServiceKey,
        '$httpParamSerializer',
        /* into */
       auditData(config),
      ],
    };
  }])
  .name;



